@-webkit-keyframes anim-glow {
	0% {
		box-shadow: 0 0 rgba(#61ef61, 1);
	}
	100% {
		box-shadow: 0 0 10px 8px transparent;
		border-width: 2px;
	}
}

@keyframes anim-glow {
	0% {
		box-shadow: 0 0 rgba(#61ef61, 1);
	}
	100% {
		box-shadow: 0 0 10px 8px transparent;
		border-width: 2px;
	}
}

